import popupLayer from '../../../../components/popup-layer.vue'
export default {
	data() {
		return {
			// 控制底部弹出框
			shows: false,
			fabu: '发布',
			result: [],
			results: []
		}
	},
	components: {
		popupLayer
	},
	methods: {
		// 返回
		goBack() {
			uni.navigateBack({
				delta: 1,
			})
		},
		// 选择单词
		onChanges(event) {
			this.result = event.detail
			console.log(this.result)
		},
		// 选择班级
		onChangess(event) {
			console.log(event)
			this.results = event.detail
			console.log(this.results)
		},
		// 控制---底部弹出框
		onClose() {
			this.shows = false
			this.fabu = '发布'
		},
		showBox() {
			this.shows = true
			this.fabu = '确认'
		}
	}
}
